.pdf-uploader-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 15px;
}

.pdf-uploader-droparea {
  border: 2px dashed #adb5bd;
  border-radius: 8px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: #f8f9fa;
}

.pdf-uploader-active {
  border-color: #007bff;
  background-color: rgba(0, 123, 255, 0.1);
}

.pdf-uploader-input {
  display: none;
}

.pdf-uploader-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pdf-uploader-icon {
  font-size: 48px;
  margin-bottom: 15px;
  color: #6c757d;
}

.pdf-uploader-text {
  font-size: 16px;
  margin-bottom: 15px;
  color: #495057;
}

.pdf-uploader-button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-bottom: 15px;
  transition: background-color 0.3s ease;
}

.pdf-uploader-button:hover {
  background-color: #0069d9;
}

.pdf-uploader-note {
  font-size: 14px;
  color: #6c757d;
}

.pdf-uploader-error {
  margin-top: 15px;
  padding: 10px;
  border-radius: 4px;
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
  text-align: center;
}

@media (max-width: 768px) {
  .pdf-uploader-droparea {
    padding: 20px;
  }
  
  .pdf-uploader-icon {
    font-size: 36px;
  }
  
  .pdf-uploader-text {
    font-size: 14px;
  }
  
  .pdf-uploader-button {
    font-size: 14px;
    padding: 8px 16px;
  }
} 